@import url("https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.min.css");

@import url("https://fonts.googleapis.com/css?family=Barlow+Semi+Condensed:800italic,700,800|Barlow:400,700,500,800");
@font-face {
  font-family: "Barlow Semi Condensed-SemiBold";
  font-style: normal;
  font-weight: 600;
  src: url("static/fonts/BarlowSemiCondensed-SemiBold.ttf") format("truetype");
}
@font-face {
  font-family: "Barlow Semi Condensed-Medium";
  font-style: normal;
  font-weight: 500;
  src: url("static/fonts/BarlowSemiCondensed-Medium.ttf") format("truetype");
}

/* The following line is used to measure usage of this code in production. For more info see our usage billing page */
@import url("https://px.animaapp.com/62266a22f9b5434b9a274ff7.621eff89b7b66445f330bdb7.v9OfNMM.rcp.png");

body {
  background-color: #141619;
}
.screen a {
  display: contents;
  text-decoration: none;
}

.container-center-horizontal {
  display: flex;
  flex-direction: row;
  justify-content: center;
  pointer-events: none;
  width: 100%;
}

.container-center-horizontal > * {
  flex-shrink: 0;
  pointer-events: auto;
}

* {
  box-sizing: border-box;
}
